<template>
	<section class="counter-area section-padding">
		<div class="container">
			<div class="row">
				<div class="col-lg-3 col-md-6 counter-item" v-for="counter in counters" :key="counter.Heading">
					<div class="single-counter">
						<div class="counter-contents">
							<h2>
								<span class="counter-number" :data-target="counter.Number">{{counter.Number}}</span>
								<span>{{counter.NumberString}}</span>
							</h2>
							<h3 class="counter-heading">{{counter.Heading}}</h3>
						</div>
					</div>
				</div>
			</div>
		</div>
	</section>
</template>

<script>
export default {
    name: "SaCounter",
    data() {
        return {
            counters: [
                {
                    Number:100,
                    NumberString:"k",
                    Heading:"Complete Projects",
                },
                {
                    Number:95,
                    NumberString:"%",
                    Heading:"Of people feel happy",
                },
                {
                    Number:500,
                    NumberString:"+",
                    Heading:"Employees Works",
                },
                {
                    Number:70,
                    NumberString:"+",
                    Heading:"5 Star Rating",
                },
            ]
        }
    },
	computed: {
		counterUp() {
			const counterWrapper = document.querySelector(".counter-area");
			const sectionOffSet = counterWrapper.offsetTop;
			window.addEventListener("scroll", function ()  {
				if(window.scrollY == sectionOffSet) {
					const counter = document.querySelectorAll(".counter-number");
					counter.forEach(counter => {
						counter.innerText = 0;
						const updateCounter = () => {
							const target = +counter.getAttribute("data-target");
							const number = +counter.innerText;
							const increment = target / 500;

							if(number < target) {
								counter.innerText = `${Math.ceil(number + increment)}`;
								setTimeout(updateCounter ,1)
							} else {
								counter.innerText = target
							}
						};

						updateCounter();
					})
				}
			});
			return 0
		}
	},
	mounted() {
		this.counterUp;
	}
}
</script>

<style scoped>
.counter-area {
	text-align: center;
	background-color: #f9f9fa;
	position: relative;
	color: #fff;
	text-align: center;
	background: url(..//assets/img/banner-bg.jpg);
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	background-attachment: fixed;
	z-index: 1;
}
.counter-area::after {
	position: absolute;
	content: "";
	top: 0;
	left: 0;
	padding: 0;
	height: 100%;
	width: 100%;
	opacity: .8;
	z-index: -1;
	background: #000;
}
.counter-area .section-title h5 {
	color: #ffffff;
}
.counter-area .section-title h2 {
	color: #fff;
}
.counter-contents h2 {
	color: #fff;
	font-size: 45px;
	font-weight: 700;
	margin-bottom: 5px;
	word-spacing: -10px;
}
.counter-contents h3 {
	color: #fff;
	font-size: 18px;
	font-weight: 700;
}

</style>