<template>
    <section id="faq" class="faq-area section-padding">
		<div class="container">
            <SectionHeader>
                <template v-slot:sectionTitle>
                    Help of the following FAQs
                </template>
                <template v-slot:sectionSubTitle>
                    Frequently Asked Questions
                </template>
            </SectionHeader>
			<div class="row">
				<div class="col-lg-10 offset-lg-1 col-md-12">
					<div class="faq-accordion first-faq">
						<ul class="accordion">
							<li class="accordion-item" v-for="faq in faqs" :key="faq.faqHead">
								<a class="accordion-title" href="javascript:void(0)">
                                    <i class="fa fa-caret-down"></i>
                                    {{faq.faqHead}}
                                </a>
								<p class="accordion-content ">{{faq.faqContent}}</p>
							</li>
						</ul>
					</div>
				</div>
			</div>
		</div>
	</section>

</template>

<script>
import SectionHeader from "../components/SectionHeader";
export default {
    name: "Faq",
    data() {
        return {
            faqs: [
                {
                    faqHead: "What do I do when my computer crashes?",
                    faqContent: "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate",
                },
                {
                    faqHead: "There is no display on the monitor, what do I do now?",
                    faqContent: "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa officia deserunt mollit anim id est laborum.",
                },
                {
                    faqHead: "How can I clean my keyboard?",
                    faqContent: "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa officia deserunt mollit anim id est laborum.",
                },
                {
                    faqHead: "Why is my computer mouse acting erratically?",
                    faqContent: "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa officia deserunt mollit anim id est laborum.",
                }
            ]
        }
    },
    components: {
        SectionHeader,
    },

    methods: {
        toggleAccordion() {
            const acc_btns = document.querySelectorAll(".accordion-title");
            const acc_contents = document.querySelectorAll(".accordion-content");

            acc_btns.forEach((btn) => {
                btn.addEventListener("click", (e) => {
                    acc_contents.forEach((acc) => {
                    if (e.target.nextElementSibling !== acc && acc.classList.contains("show")) {
                        acc.classList.remove("show");
                        acc_btns.forEach((btn) => btn.classList.remove("active"));
                    }
                });

                    const panel = btn.nextElementSibling;
                    panel.classList.toggle("show");
                    btn.classList.toggle("active");
                });
            });

            window.onclick = (e) => {
                if (!e.target.matches(".accordion-title")) {
                    acc_btns.forEach((btn) => btn.classList.remove("active"));
                    acc_contents.forEach((acc) => acc.classList.remove("show"));
                }
            };
        }
    },

    mounted() {
        this.toggleAccordion()
    }
};

</script>

<style scoped>

.faq-accordion .accordion {
    list-style-type: none;
    padding: 0;
    margin: 0;
    padding-left: 0;
    margin-bottom: 0;
    position: relative;
}

.faq-accordion .accordion .accordion-item {
    display: block;
    border-radius: 0;
    margin-bottom: 20px;
    overflow: hidden;
}

.faq-accordion .accordion .accordion-item:last-child {
    margin-bottom: 0;
}

.faq-accordion .accordion .accordion-item .accordion-title {
    padding: 20px;
    color: #333;
    position: relative;
    background: #f5f5f6;
    font-size: 16px;
    font-weight: 700;
    display: block;
    border: none;
}

.faq-accordion .accordion .accordion-item .accordion-title i {
    position: absolute;
    right: 30px;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    color: #767c99;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    font-size: 15px;
}

.faq-accordion .accordion .accordion-item .accordion-title.active i::before {
    content: "\f0d8";
}

.faq-accordion .accordion .accordion-item .accordion-content {
    background-color: #ffffff;
    margin-bottom: 0;
    font-size: 15px;
    overflow: hidden;
    height: 0;
    transition: 0.5s ease;
    padding-right: 15px;
    padding-left: 15px;
}

.faq-accordion .accordion .accordion-item .accordion-content.show {
    height: auto;
    padding-top: 20px;
    padding-bottom: 15px;
}
</style>
